const state = {
    storeID: 1,
    storeName: 'GroundMenu.com',
    seatingCapacity: 1,
    takeoutSupport: false,
};

const getters = {
    storeName: state => state.storeName,
    seatingCapacity: state => state.seatingCapacity,
    takeoutSupport: state => state.takeoutSupport,
};

const mutations = {
    storeNameChange(state, newStoreName){
        state.storeName = newStoreName
    },
    seatingCapacityChange(state, newSeatingCapacity){
        state.seatingCapacity = newSeatingCapacity
    },
    takeoutSupportChange(state, newTakeoutSupport){
        console.log(newTakeoutSupport)
        state.takeoutSupport = newTakeoutSupport
    }
};

const actions ={
    storeNameChange({ commit }, newStoreName){
        commit("storeNameChange", newStoreName)
    },
    seatingCapacityChange({ commit }, newSeatingCapacity){
        commit("seatingCapacityChange", newSeatingCapacity)
    },
    takeoutSupportChange({ commit }, newTakeoutSupport){
        console.log(newTakeoutSupport)
        commit("takeoutSupportChange", newTakeoutSupport)
    }
};

export default {
    state,
    getters,
    mutations,
    actions,
}